import { BufferGeometry, Mesh } from 'three';
import { IGameObject } from './IGameObject';
import { IMaterial } from './IMaterial';
import { Signal } from '../utils/Signal';

export interface IMeshConfig {
    geometry?: BufferGeometry;
    material?: IMaterial;
}

export interface StandardMeshConfig extends IMeshConfig {
    x?: number;
    y?: number;
    z?: number;
    rotX?: number;
    rotY?: number;
    rotZ?: number;
    scaleX?: number;
    scaleY?: number;
    scaleZ?: number;
    visible?: boolean;
}

export class StandardMesh<T extends StandardMeshConfig = StandardMeshConfig> extends Mesh implements IGameObject {
    public raycasterSignal = new Signal();

    protected _config: T;

    constructor(config: T) {
        super(config.geometry, config.material);

        this._config = config;
    }

    public buildObject(): void {
        this._setBaseConfig(this);
    }

    public reset(): void {
        this._setBaseConfig(this);
    }

    /* eslint-disable */
    private _setBaseConfig(ctx: any): void {
        const config = ctx._config;
        const { x, y, z, rotX, rotY, rotZ, scaleX, scaleY, scaleZ, visible } = config;

        if (typeof x === "number") ctx.position.x = x;
        if (typeof y === "number") ctx.position.y = y;
        if (typeof z === "number") ctx.position.z = z;

        ctx.rotation.set(0, 0, 0);
        ctx.quaternion.set(0, 0, 0, 1);
        if (typeof rotX === "number") ctx.rotation.x = rotX;
        if (typeof rotY === "number") ctx.rotation.y = rotY;
        if (typeof rotZ === "number") ctx.rotation.z = rotZ;

        if (typeof scaleX === "number") ctx.scale.x = scaleX;
        if (typeof scaleY === "number") ctx.scale.y = scaleY;
        if (typeof scaleZ === "number") ctx.scale.z = scaleZ;

        if (typeof visible === "boolean") ctx.visible = visible;
    }

    /* eslint-disable */
    public updateObject(dt: number): void {
        // pass
    }
    /* eslint-enable */

    public onPointed(): void {
        console.log("click");
        this.raycasterSignal.dispatch()
    }
}